@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600;900&display=swap");
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#bgc{width:100vw;min-height:100vh;background:linear-gradient(154.85deg, #3BBCEE 0%, #4F54CF 110.65%)}.main{width:70vw;height:85vh;position:absolute;background-color:#fff;border-radius:12px;top:50%;left:50%;transform:translate(-50%, -50%);font-family:'Montserrat', sans-serif;overflow-y:scroll}.input{margin:10px 20px}input[type=button]{background-color:#9AD042;color:#fff;border-radius:6px;border:none;height:60px;min-width:30%;font-style:normal;font-weight:600;font-size:20px;line-height:110%;cursor:pointer}input[type=text]{background-color:#FFFFFF;border:1px solid #AFAFAF;padding-left:10px;margin-bottom:10px;font-style:normal;font-weight:normal;font-size:20px;line-height:110%;box-sizing:border-box;margin-right:5%;border-radius:6px;height:60px;min-width:65%}@media (max-width: 510px){.main{text-align:center}}

.info{margin:20px;height:auto;display:flex;flex-wrap:wrap;text-align:left}.data{min-width:50%}.data span{display:inline-block;line-height:30px;margin-left:5px;transform:translateY(-25%)}.data span:nth-child(odd){color:#AFAFAF}@media (max-width: 600px){.data{min-width:51%}}@media (min-width: 1024px){.data{min-width:33%}}

.logo{background-image:url(data:image/png;base64,);background-repeat:no-repeat;height:48px;width:41px;margin-top:10px;margin-left:50%;transform:translateX(-50%)}.title{display:block;padding:10px;font-style:normal;font-weight:900;font-size:38px;line-height:110%;text-align:center;color:#4A4A4A}.subtitle{display:block;padding:10px;font-style:normal;font-weight:normal;font-size:20px;line-height:110%;text-align:center;color:#4A4A4A}

.footer{position:absolute;bottom:0;height:20px;font-size:12px;line-height:20px;color:#FFFFFF;padding:10px;text-align:center;width:100%}

